import React, { useState } from "react";
import Text from "components/Text";
import Spinner from "components/Spinner";
import CheckBox from "components/CheckBox";
import IconButton from "@material-ui/core/IconButton";
import FavoriteIcon from "@material-ui/icons/Favorite";
import * as S from "../../components/UserList/style";
import * as S_Home from "../../pages/Home/style";

const Favorites = () => {
  const [hoveredUserId, setHoveredUserId] = useState();
  const [favUsers, setFavUsers] = useState(JSON.parse(localStorage.getItem("favorites")));

  const handleMouseEnter = (index) => {
    setHoveredUserId(index);
  };

  const handleMouseLeave = () => {
    setHoveredUserId();
  };

  const AddRemoveUsersFromFavs = (userIndex) => {
    favUsers.splice(userIndex, 1);

    localStorage.setItem("favorites", JSON.stringify(favUsers));
    setFavUsers(favUsers);
  };

  return (
    <S_Home.Home>
    <S_Home.Content>
    <S_Home.Header>
          <Text size="64px" bold>
            Favorite Users
          </Text>
    </S_Home.Header>
    <S.UserList>
      <S.List>
        {favUsers.map((user, index) => {
          return (
            <S.User
              key={index}
              onMouseEnter={() => handleMouseEnter(index)}
              onMouseLeave={handleMouseLeave}
            >
              <S.UserPicture src={user?.picture.large} alt="" />
              <S.UserInfo>
                <Text size="22px" bold>
                  {user?.name.title} {user?.name.first} {user?.name.last}
                </Text>
                <Text size="14px">{user?.email}</Text>
                <Text size="14px">
                  {user?.location.street.number} {user?.location.street.name}
                </Text>
                <Text size="14px">
                  {user?.location.city} {user?.location.country}
                </Text>
              </S.UserInfo>
              <S.IconButtonWrapper isVisible={true}>
                <IconButton onClick={_ => AddRemoveUsersFromFavs(hoveredUserId)}>
                  <FavoriteIcon color="error" />
                </IconButton>
              </S.IconButtonWrapper>
            </S.User>
          );
        })}
      </S.List>
    </S.UserList>
    </S_Home.Content>
    </S_Home.Home>
  );
};

export default Favorites;